<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
export default {
  name: 'GxButtonActions',
  data() {
    return {
      showItems: false,
      methodFile: '',
      extension: '',
    }
  },
  props: {
    buttonsActions: {
      Type: Array,
      default: null,
      //Exemple
      // [{
      //   name: //texto a mostrar
      //   icon: //icono a mostrar de material designed
      //   type: //tipo de elemento: download, link, file, method
      //   action:  //ruta de archivo o enlace a dirigir
      //   additionalData  // extension file, name file download
      // }]
    },
  },
  components: {
    GxIcon,
  },
  computed: {
    getExtensionsList() {
      if (this.extension !== '') {
        return this.extension
      } else {
        return '.*'
      }
    },
  },
  mounted() {
    const clickOutside = (evt) => {
      if (!this.$el.contains(evt.target)) {
        this.showItems = false
      }
    }
    document.addEventListener('click', clickOutside)
    this.$once('hook:beforeDestroy', () => {
      document.removeEventListener('click', clickOutside)
    })
  },
  methods: {
    clickMenu() {
      this.showItems = !this.showItems
    },
    clickMenuOption(option) {
      this.methodFile = ''
      this.extension = ''
      switch (option.type) {
        case 'method':
          this.$emit(option.action)
          break
        case 'link':
          window.open(option.action, '_blank')
          break
        case 'download':
          window.open(option.action, 'Download')
          break
        case 'file':
          this.methodFile = option.action
          this.extension = option.additionalData
          setTimeout(() => {
            this.$refs.uploadFileButtonActions.click()
            this.showItems = false
          }, 10)
          break
      }
      this.showItems = false
    },
    async addFileButton(event) {
      this.file = event.target.files[0]
      this.getBase64()
    },
    getBase64() {
      this.fileName = this.file.name
      this.loadingFile = true
      let reader = new FileReader()
      reader.onloadend = () => {
        // Since it contains the Data URI, we should remove the prefix and keep only Base64 string
        this.fileBase64 = reader.result.replace(/^data:.+;base64,/, '')
        this.$emit(this.methodFile, this.fileBase64, this.fileName)
        this.methodFile = ''
        this.extension = ''
      }
      reader.readAsDataURL(this.file)
    },
  },
}
</script>
<template>
  <div class="relative">
    <transition name="fade">
      <div
        v-show="this.showItems"
        class="mt-4 absolute border border-gray-700 rounded-lg shadow-4 right-4 origin-top-right text-left z-1"
      >
        <div
          class="whitespace-no-wrap bg-white rounded-lg shadow-lg cursor-pointer"
        >
          <div class="border-gray-200 py-1.5">
            <div
              class="flex content-center h-7.5 text-gray-700 hover:bg-otherOptions-hover active:bg-otherOptions-click align-middle px-2 py-1"
              v-for="item in this.buttonsActions"
              :key="item.name"
              @click="clickMenuOption(item)"
            >
              <GxIcon
                class="align-middle ml-1.5 mr-1.5 pr-1 pt-px"
                :icon="item.icon"
                v-bind:style="{ fontSize: '19px' }"
              />
              <span class="align-middle pr-1.5 text-sm">
                {{ item.name }}</span
              ></div
            >
          </div>
        </div>
      </div>
    </transition>
    <div
      class="flex flex-col content-center justify-center rounded-lg w-7.5 h-7.5 text-center text-gray-500 border-gray-500 border-1 cursor-pointer hover:bg-otherOptions-hover active:bg-otherOptions-click bg-white"
      @click="this.clickMenu"
    >
      <input
        type="file"
        class="hidden"
        id="uploadFileButtonActions"
        ref="uploadFileButtonActions"
        @change="addFileButton($event)"
        :accept="getExtensionsList"
      />
      <div class="flex justify-center pt-px pl-px">
        <span class="text-2xs">•••</span>
      </div>
    </div>
  </div>
</template>
