<script>
export default {
  name: 'GxIncomeStatement',
  props: {
    incomeStatement: {
      type: Object,
      default: null,
      required: true,
    },
    periodId: {
      type: Number,
      default: 0,
      required: true,
    },
  },
  computed: {
    getNamePeriod() {
      let months = [
        'Enero',
        'Febrero',
        'Marzo',
        'Abril',
        'Mayo',
        'Junio',
        'Julio',
        'Agosto',
        'Septiembre',
        'Octubre',
        'Noviembre',
        'Diciembre',
      ]

      return months[Number(this.periodId.toString().slice(4)) - 1]
    },
    getYear() {
      return this.periodId.toString().slice(0, 4)
    },
  },
  methods: {
    getNumberFormat(text) {
      if (text === '') return '-'
      if (text === '0') return '-'
      if (text === '0.00') return '-'
      if (text.includes('.')) {
        return text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\d(?=(\d{3})+\.)/g, '$&,')
      } else {
        //return (
        text = text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\.\d$/, '0$&0')
          .replace(/\d+\.$/, '$&00')
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        if (text == '0') return ''
        if (text == '0' || text == '') return text
        return text + '.00'
      }
    },
  },
}
</script>
