<script>
import GxPeriodsDropdown from '@components/_CoreTaxes/Periods/GxPeriodsDropdown'
import GxButtonActions from '@components/_CoreTaxes/Buttons/ButtonActions/GxButtonActions'
import GxDropdown from '@components/_Core/Forms/GxDropdown'
import GxIncomeStatement from '@components/_CoreTaxes/Reports/IncomeStatement/GxIncomeStatement'

// import GxButtonTaxes from '@components/_CoreTaxes/Buttons/Button/GxButtonTaxes'
// import GxModal from '@components/_Core/Modals/GxModal'
// import GxLoader from '@components/_Core/Utils/GxLoader'

import {
  GET_INCOME_STATEMENT,
  GET_INCOME_STATEMENT_PDF_OR_EXCEL,
} from '@src/apollo/queries'

export default {
  name: 'IncomeStatement',
  data() {
    return {
      incomeStatement: null,
      beginningPeriod: null,
      endingPeriod: null,
      beginningPeriodId: null,
      endingPeriodId: null,
      showZeroBalanceAccounts: 'No',
      showPercentages: 'No',
      skipIncomeStatement: true,
      typeFileDownload: '',
    }
  },
  components: {
    GxPeriodsDropdown,
    GxDropdown,
    GxButtonActions,
    GxIncomeStatement,
  },
  apollo: {
    //  $clientNumber: [ID]
    // $periodId: Int!
    // $showZeroBalanceAccounts: Boolean!
    // $showPercentages: Boolean
    // $assumeLastAnnualPeriodIsClosed: Boolean
    getIncomeStatement: {
      query: GET_INCOME_STATEMENT,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          beginningPeriodId: this.beginningPeriodId,
          endingPeriodId: this.endingPeriodId,
          showZeroBalanceAccounts:
            this.showZeroBalanceAccounts === 'Si' ? true : false,
          showPercentages: this.showPercentages === 'Si' ? true : false,
          assumeLastAnnualPeriodIsClosed: true,
        }
      },
      update(data) {
        this.incomeStatement =
          data.myAccountingFirms[0].clients[0].accounting.reports.incomeStatement
        //console.log(this.incomeStatement)
      },
      skip() {
        return this.skipIncomeStatement
      },
      fetchPolicy: 'no-cache',
    },
    getIncomeStatementPDFOrExcel: {
      query: GET_INCOME_STATEMENT_PDF_OR_EXCEL,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          beginningPeriodId: this.beginningPeriodId,
          endingPeriodId: this.endingPeriodId,
          showZeroBalanceAccounts:
            this.showZeroBalanceAccounts === 'Si' ? true : false,
          showPercentages: this.showPercentages === 'Si' ? true : false,
          assumeLastAnnualPeriodIsClosed: true,
          fileType: this.typeFileDownload,
        }
      },
      update(data) {
        window.open(
          data.myAccountingFirms[0].clients[0].accounting.reports
            .incomeStatementExport.url,
          'Download'
        )
        this.typeFileDownload = ''
      },
      skip() {
        return this.typeFileDownload === ''
      },
      fetchPolicy: 'no-cache',
    },
  },
  computed: {
    getButtonsActions() {
      let actions = [
        {
          name: 'Descargar balance general en Excel',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadExcel',
        },
        {
          name: 'Descargar balance general en PDF',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadPDF',
        },
      ]
      return actions
    },
  },
  created() {
    this.beginningPeriod = localStorage.getItem('beginningPeriod')
    this.endingPeriod = localStorage.getItem('endingPeriod')
    if (localStorage.getItem('periodDefault')) {
      this.beginningPeriodId = Number(localStorage.getItem('periodDefault'))
      this.endingPeriodId = Number(localStorage.getItem('periodDefault'))
    } else {
      this.beginningPeriodId = this.endingPeriod
      this.endingPeriodId = this.endingPeriod
    }
    setTimeout(() => {
      this.$refs.PeriodInit.changeValue(this.beginningPeriodId)
      this.$refs.PeriodEnd.changeValue(this.endingPeriodId)
    }, 50)
  },
  methods: {
    selectedPeriodInit(period) {
      //console.log(period)
      this.skipIncomeStatement = true
      this.incomeStatement = null
      if (
        period > this.endingPeriodId ||
        period.toString().slice(0, 4) !==
          this.endingPeriodId.toString().slice(0, 4)
      ) {
        this.$refs.PeriodEnd.changeValue(period)
        this.endingPeriodId = period
      }
      this.beginningPeriodId = period
      this.skipIncomeStatement = false
    },
    selectedPeriodEnd(period) {
      // console.log(period)
      this.skipIncomeStatement = true
      this.incomeStatement = null

      if (
        period < this.beginningPeriodId ||
        period.toString().slice(0, 4) !==
          this.beginningPeriodId.toString().slice(0, 4)
      ) {
        this.beginningPeriodId = period
        this.$refs.PeriodInit.changeValue(period)
      }
      this.endingPeriodId = period
      this.skipIncomeStatement = false
    },
    downloadPDF() {
      this.typeFileDownload = 'pdf'
    },
    downloadExcel() {
      this.typeFileDownload = 'excel'
    },
  },
}
</script>
<template>
  <div class="flex flex-col w-full px-10 justify-center pb-15">
    <div class="flex flex-col w-full wrapper">
      <div class="flex title text-2xl">
        Estado de Resultados
      </div>

      <div class="flex -ml-4 juditfy-between">
        <div class="flex w-full">
          <div class="flex w-51">
            <GxPeriodsDropdown
              v-if="beginningPeriod && endingPeriod"
              ref="PeriodInit"
              :beginningPeriod="beginningPeriod"
              :endingPeriod="endingPeriod"
              class="mt-2"
              placeholder="Período inicial"
              label="Período inicial"
              :allow-empty="false"
              @selectedPeriod="selectedPeriodInit"
            />
          </div>
          <div class="w-51 -ml-4">
            <GxPeriodsDropdown
              v-if="beginningPeriod && endingPeriod"
              ref="PeriodEnd"
              :beginningPeriod="beginningPeriod"
              :endingPeriod="endingPeriod"
              class="mt-2"
              placeholder="Período final"
              label="Período final"
              :allow-empty="false"
              @selectedPeriod="selectedPeriodEnd"
            />
          </div>
          <div class="w-51 -ml-4">
            <GxDropdown
              watch-value
              v-model="showZeroBalanceAccounts"
              class="mt-2"
              placeholder="Mostrar cuentas en ceros"
              label="Mostrar cuentas en ceros"
              :options="['No', 'Si']"
            />
          </div>
          <div class="w-51 -ml-4 hidden">
            <GxDropdown
              watch-value
              v-model="showPercentages"
              class="mt-2"
              placeholder="Mostrar porcentajes"
              label="Mostrar porcentajes"
              :options="['No', 'Si']"
            />
          </div>
        </div>
        <div class="flex">
          <GxButtonActions
            class="mt-5"
            v-if="this.beginningPeriod"
            :buttonsActions="getButtonsActions"
            @downloadPDF="downloadPDF"
            @downloadExcel="downloadExcel"
          />
        </div>
      </div>
      <div
        class="flex w-full mt-10"
        v-if="this.$apollo.queries.getIncomeStatement.loading"
        ><GxLoader />
      </div>
      <div v-if="incomeStatement" class="flex w-full -mt-4">
        <GxIncomeStatement
          :incomeStatement="incomeStatement"
          :periodId="endingPeriodId"
        />
      </div>
    </div>
  </div>
</template>
