<script>
import GxDropdown from '@components/_Core/Forms/GxDropdown'

export default {
  name: 'GxPeriodsDropdown',
  data() {
    return {
      periodId: null,
      period: null,
      selected: '',
      months: [
        'Enero',
        'Febrero',
        'Marzo',
        'Abril',
        'Mayo',
        'Junio',
        'Julio',
        'Agosto',
        'Septiembre',
        'Octubre',
        'Noviembre',
        'Diciembre',
      ],
      listPeriods: null,
    }
  },
  props: {
    defaultYear: {
      type: Number,
      default: null,
      required: false,
    },
    defaultMonth: {
      type: Number,
      default: null,
      required: false,
    },
    beginningPeriod: {
      type: String,
      default: null,
      required: true,
    },
    endingPeriod: {
      type: String,
      default: null,
      required: true,
    },
    label: {
      type: String,
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
  },
  components: {
    GxDropdown,
  },
  watch: {
    selected(newVal) {
      if (newVal) {
        let item = newVal.split(' ')
        this.$emit(
          'selectedPeriod',
          Number(
            item[1].trim() +
              ('0' +
                (this.months.indexOf(item[0].trim()) + 1).toString().slice(-2))
          )
        )
      }
    },
  },
  created() {
    this.listPeriods = this.getListPeriods()
    if (this.defaultYear !== null && this.defaultMonth !== null) {
      this.selected =
        this.months[this.defaultMonth - 1] + ' ' + this.defaultYear
    }
  },
  methods: {
    getListPeriods() {
      let yearInit = Number(this.beginningPeriod.toString().substring(0, 4))
      let monthInit = Number(this.beginningPeriod.toString().substring(4))

      let yearEnd = Number(this.endingPeriod.toString().substring(0, 4))
      let monthEnd = Number(this.endingPeriod.toString().substring(4))
      let rangePeriods = new Array()

      while (yearInit <= yearEnd) {
        for (
          0;
          monthInit <= (yearInit === yearEnd ? monthEnd : 12);
          monthInit++
        ) {
          rangePeriods.push(this.months[monthInit - 1] + ' ' + yearInit)
        }
        monthInit = 1
        yearInit++
      }
      return rangePeriods
    },
    changeValue(periodId) {
      periodId = periodId.toString()
      this.selected =
        this.months[Number(periodId.slice(4)) - 1] + ' ' + periodId.slice(0, 4)
    },
  },
}
</script>

<template>
  <GxDropdown
    watch-value
    v-model="selected"
    class="mt-2"
    :options="listPeriods"
    :placeholder="placeholder"
    :label="label"
    :allow-empty="false"
  />
</template>
